<template>
  <div id="home-user">
    <p>HomeUser组件</p>
    <img src="@/assets/logo.png">
    <p>切换组件后，组建之中的数据会及时更新</p>
    <p>{{msg}}</p>
    <p class="add-list-item">
      <button @click="deleteAll">先清除store，再清除localS</button>
      <button @click="quit">退出登录</button>
    </p>
  </div>
</template>

<script type="text/javascript">
import store from '@/store/store'
export default {
  name: 'home组件',
  data () {
    return {
      msg: store.state.count
    }
  },
  created () {
    if (store.state.status === false) {
      this.$router.push('/')
    }
  },
  methods: {
    deleteAll () {
      store.commit('clearAll')
    },
    quit () {
      store.commit('changeStatus')
      this.$router.push('/')
    }
  }
}
</script>

<style type="text/css">
#home-user {
  border: 1px solid brown
}
.add-list-item {
  position: fixed;
  left: 50%;
  bottom: 10%;
  width: 60%;
  transform: translateX(-50%);
}
.add-list-item button {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: rgba(172, 178, 107, .5);
  border-radius: 5px;
  text-decoration: none;
}
</style>



